import axios from "axios";
import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { GetWishlist, removeWishlist } from "../actions/wishlistAction";
import { BsHeartFill } from "react-icons/bs";
import { toast } from "react-toastify";

const Wishlist = () => {
  const [wishlist, setWishlist] = useState([]);
  const [loading, setLoading] = useState(false);
  const { user } = useSelector((state) => ({ ...state }));

  useEffect(() => {
    loadWishlist();
    toast.info("Tap on heart to remove product from wishlist");
  }, []);

  const loadWishlist = async () => {
    setLoading(true);
    await axios
      .get(`${process.env.REACT_APP_API}/wishlist`, {
        headers: {
          authtoken: user.token,
        },
      })
      .then((res) => {
        setLoading(false);
        setWishlist(res.data.wishlist);
      });
  };

  const handleRemove = (productId) => {
    removeWishlist(productId, user.token).then((res) => {
      loadWishlist();
    });
  };


  return (
    <section class="text-gray-600 body-font">
      <div class="container px-5 py-10 mx-auto">
        <div class="flex flex-col md:pr-10 md:mb-0 mb-6 pr-0 w-full md:w-auto  text-center">
          <h2 class="text-xs text-indigo-500 tracking-widest font-medium title-font mb-1">
            YOUR
          </h2>
          <h1 class="md:text-3xl text-2xl font-medium title-font text-gray-700">
            Wishlist
          </h1>
        </div>
        <div class="flex flex-wrap lg:w-4/5 sm:mx-auto sm:mb-2 -mx-2">
          {loading && <p> loading... </p>}
          {wishlist && wishlist.length === 0 ? (
            <p> no items in wishlist </p>
          ) : (
            wishlist.map((x) => (
              <div class="p-2 sm:w-1/2 w-full">
                <div class="bg-gray-100 rounded flex p-4 h-full items-center">
                  <BsHeartFill
                    style={{ cursor: "pointer" }}
                    size={20}
                    color="blue"
                    className="mx-2"
                    onClick={() => handleRemove(x._id)}
                  />
                  <span class="title-font font-medium">
                    <strong>
                      <Link to={`/product/${x._id}`}>{x.name}</Link>
                    </strong>
                  </span>
                </div>
              </div>
            ))
          )}
        </div>
      </div>
    </section>
  );
};

export default Wishlist;
